import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import axios from "axios"
import Highcharts from 'highcharts'
import HC_more from 'highcharts/highcharts-more'
import HC_data from 'highcharts/modules/data'
import HighchartsReact from 'highcharts-react-official'

import "tabler-react/dist/Tabler.css";

import { Card, Page } from "tabler-react";

class privateOPDprice extends React.Component {
  
  fetchOnline(){    
    var _this = this;
    
      axios
        .get(`https://ourhealthcarehk.github.io/api/PRIVATEOPDPRICE.json`)
        .then(({ data }) => {
          
          
          console.log(data);
          
          if (!data)
            return;
          
          _this.state.data.rows = data;
          _this.setState(_this.state);
                    
        })
        .catch(error => {
          _this.setState({ loading: false, error })
        })
  }
  
  constructor() {
    super()
    if (typeof Highcharts === 'object') {
      HC_more(Highcharts);
      HC_data(Highcharts);
    }
    this.state = {
      "chart": {
          "type": "columnrange",
          "inverted": true,
          "polar": false
      },
      "plotOptions": {
          "series": {
              "animation": false,
              "minPointLength": 5,
              "lineWidth": 5,
              "dataLabels": {
                  "enabled": true,
                  "style": {
                      "color": "contrast",
                      "fontSize": "11px",
                      "fontWeight": "",
                      "textOutline": "1px 1px contrast"
                  }
              }
          }
      },
      "title": {
          "text": ""
      },
      "subtitle": {
          "text": ""
      },
      "credits": {
          "enabled": false
      },
      "tooltip": {
          "shared": true
      },
      "data": {}
  };
    
  }
  
	componentDidMount() {
    this.fetchOnline();
	}
  
	render() {
    
      return (
      <Layout>
    <SEO title="私立醫院門診收費一覽" description="香港各大私立醫院的門診收費，分別顯示日診及通宵夜診間的收費。"/>
    <Page.Content title="私立醫院門診收費一覽">
      <Card>
          <Card.Body>
        <HighchartsReact style={{ width: "100%", minHeight: "100px"}}
        highcharts={Highcharts}
        options={this.state}
      />
        <p>註：</p>
        <p>1. 以上收費均以各醫院最新公布為準</p>
        <p>2. 上列收費為基本門診收費，並未包含藥物、行政、或額外檢查所產生之費用。</p>
        <p>3. 由於某部分醫院提供長者、醫療卡優惠，最終最低收費或會有不同。</p>
        </Card.Body>
      </Card>
    </Page.Content>
      </Layout>
      );
  }
}

export default privateOPDprice
